.container {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}


.authWrapper {
    width: 503px;
    height: 379px;
    border-radius: 44px;
    background-color: #e990f9;
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;

}

img .bearFace {
    width: 160px;
    height: 150px;
    margin: 0 0 56px;
    padding: 0.1px 0.2px 0px 0.2px;
    object-fit: contain;
    box-sizing: border-box;

}

.loginButton {
    height: 60px;
    margin: 56px 5px;
    padding: 20px 34px 19px;
    border-radius: 36px;
    border: solid 3px #fff;
    background: transparent;
}

.letMeIn {
    height: 21px;
    font-size: 18px;
    font-weight: bold;
    font-family: Roboto, Helvetica, sans-serif;
    color: #ffffff;
}

@media screen and (max-width: 1000px) {
    .authWrapper {
        width: 90%;
        background: none;
    }

    .loginButton {
        border: solid 3px #e990f9;
    }

    .letMeIn {
        color: #e990f9;
    }
}